@import '../styles/core/variables';
.header {
  font-family: $fontFamilyPrime;
  font-weight: 600;
  position: fixed;
  width: 100%;

  background-color: rgb(148, 101, 144);
  z-index: 100;
  display: flex;
  justify-content: flex-end;

  &__links {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    margin: 1em;
  }
  &__link {
    display: inline-block;
    text-decoration: none;
    margin: 0 1em 0 1em;
    &__home {
      margin: 5px;
      margin-right: 25px;
      font-size: 25px;
    }
  }
}
.fa-home {
  transform: translateY(-5px);
}
