@import '../styles/core/variables';
.aboutMe {
  font-family: $fontFamilyPrime;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr, 1fr, 1fr;
  padding: 70px 12px;
  background-color: $primeColorAboutMe;
  text-align: center;
  @media (min-width: 768px) {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr, 2fr;
    &__title {
      grid-column: span 3;
    }
  }

  &__allDescription {
    padding: 30px 0px 70px 0px;
    @media (min-width: 768px) {
      margin: 10px;
      grid-column: 1/3;
    }
  }
  &__description {
    text-align: justify;
    margin: 10px;
  }
}
.skill {
  text-align: center;
  margin-bottom: 40px;
  &__list {
    margin-top: 20px;
    list-style-type: none;
    &__item {
      display: inline-block;
      margin: 3px;
      padding: 5px;
      background-color: rgb(223, 200, 240);
      border-radius: 10px;
      border: 1px solid purple;
      box-shadow: 2px 2px purple;
    }
  }
  @media (min-width: 768px) {
    grid-column: 3/4;
    margin: 10px;
    padding: 30px 0px 70px 0px;
  }
}
